@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");

* {
    margin: 0;
    border: 0;
    box-sizing: border-box;
}
a {
    color: var(--text-color);
    text-decoration: none;
    ;
}
:root {
    --font-roboto: "Roboto Slab", serif;
    --font-raleway: "Raleway", sans-serif;
}


body {
    font-family: var(--font-roboto);
  
}
/* Menu */
header {
    background: linear-gradient( 0deg, #222527 0%, #2F2E2E 100% );
    /*  background-color: #2F2E2E;*/
    width: 100vw;
    height: 160px;
}
.barraMenu {
    background: linear-gradient( 0deg, #111111 0%, #222527 100% );
    height: 50px;
    width: 100vw;
    text-align: center;
    z-index: 3;
}
.menuOutline {
    float:right;
    margin-inline:60px;
    margin-block:8vh;
    overflow:hidden;
    
}
.title2 {
display:none;

}
@media screen and (max-width: 512px) {
    .menuOutline {
        display: none !important;
    }
    .title {
        display: none !important;
    }
    .title2 {
        display: inline-block !important;
     
    }
    .header {
        text-align:center !important;
    }
}
@media screen and (max-width: 600px) {
    .menuOutline {
        display: none !important;
    }

    .title {
        display: none !important;
    }

    .title2 {
        display: inline-block !important;
        text-align:center;
    }

    header {
        text-align: center !important;
    }
    h1 {
        text-align: center !important;
        margin-left: -10px !important;
    }
}

.menuEntry {
    padding-inline: 0px;
    margin-block: 5px;
    display: inline-block;
    background: linear-gradient( 90deg, rgba(249, 211, 180, 1) 0%, rgba(249, 211, 180, 0.6) 100% );
    background-size: 100vw 100vh;
    height: 30px;
}
    .menuEntry:hover {
    filter: brightness(1.1);
    }

.menuEntryText {
    text-align: center;
    font-size:20px

}
.app {
    display: flex;
    min-height: 100vh;
    height:fit-content;
    width: 100vw;
    /* justify-content: center;*/
    /* align-items: center;*/
    flex-direction: column;
    background-image: url("textures/concrete-wall-darker.jpg");
    background-size: 100vw 100vh;
}

    hr {
        display: block;
        overflow: hidden;
        border-style: inset;
        border-width: 1px;
    }

    h3 {
        text-align: center;
    }

    h1 {
        margin-top: 60px;
        margin-left: 40px;
        text-align: center;
        display: inline-block;
        font-size: 2rem;
        letter-spacing: 0.9px;
        background: linear-gradient( 90deg, rgba(249, 211, 180, 1) 0%, rgba(249, 211, 180, 0.6) 100% );
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        width: fit-content;
    }
    /*dropdown*/
    .dropdown {
        position: absolute;
        background: #2F2E2E01;
        transform: translateY(1%);
        padding: 0.1rem;
    }

        .dropdown:hover {
            filter: none;
        }

    .menu-item {
        height: 40px;
        display: flex;
        align-items: center;
        border-radius: var(--border-radius);
        transition: background var(--speed);
        padding: 0.4rem;
        border: solid;
        border-radius: 5px;
        line-height: 15px;
        background: linear-gradient( 90deg, rgba(249, 211, 180, 1) 0%, rgba(249, 211, 180, 1) 100% );
        z-index: 3;
    }

        .menu-item:hover {
            background-color: #525357;
        }

    .franjaImages {
        width: 100vw;
    }
    /*polaImages*/
    .viewer {
        position: absolute;
        display: flex;
    }

    .polaParent {
        /*position: relative;*/
        /* margin: 5vw;*/
        z-index: 0;
        /*  display: flex;*/
        width: 40.7vw;
        height: 49.1vw;
    }

        .polaParent:hover {
            filter: brightness(1.2);
        }

    .polaAnimController {
        animation-name: enter;
        animation-duration: 0.4s;
        animation-fill-mode: forwards;
    }

    .polaAnimController2 {
        animation-name: hoveringPola;
        animation-duration: 1s;
        animation-fill-mode: forwards;
    }

        .polaAnimController2:hover {
            animation-name: hoveringPola;
            animation-duration: 1s;
            animation-direction: reverse;
        }


    .polaAnimControllerOut {
        animation-name: enter;
        animation-duration: 0.4s;
        animation-direction: reverse;
        animation-fill-mode: forwards;
    }


    .polaImage0 {
        box-shadow: 0 0 150px #000;
        position: absolute;
        width: 40.7vw;
        height: 49.1vw;
    }

    .polaImage1 {
        position: absolute;
        width: 36vw;
        height: 36vw;
        margin: 2.4vw;
        margin-top: 2.6vw;
    }

    .polaImage2 {
        position: absolute;
        width: 10vw;
        height: 10vw;
        margin: 15vw;
        margin-top: -3vw;
        filter: brightness(1.2) hue-rotate(60deg) saturate(80%);
    }
    /*Viewer style*/
    .franjaProject {
        object-fit: cover;
        width: 100vw;
    }
    /*Enter Animation*/
    @keyframes enter {
        from {
            transform: translateX(500vw);
        }

        to {
            transform: translateX(0);
        }
    }

    @keyframes hoveringPola {
        from {
            transform: translateY(0px);
        }

        to {
            transform: translateY(10px);
        }
    }



    /*ProjectOverView*/
    .bodyProject {
        scroll-padding: 10px;
        /* background:white;*/
    }

    /* PlanetProject */
    .projectPlanet {
        position: absolute;
        background: #f8d2b3;
    }

    .bodyp {
        padding: 20px;
    }

    .titleProject {
        font-size: 30px;
        text-align: center;
        padding-bottom: 5px;
        background: #c4a991;
    }

    .titleProject2 {
        font-size: 25px;
        text-align: center;
        padding-bottom: 5px;
        background: #c4a991;
    }

    @media screen and (max-width: 750px) {
        .file {
            display: block !important;
        }

        .columnImage {
            width: 100vw !important;
            height: 100vw !important;
        }

        .columnImage1920 {
            width: 100vw !important;
            height: 56.25vw !important;
        }

        .columnImage1920full {
            width: 100vw !important;
            height: 56.25vw !important;
        }

        .youtubeBox {
            width: 100vw !important;
            height: 56.25vw !important;
        }
    }

    .file {
        display: flex;
        background: #f0cbae;
        margin-top: 5px;
    }

    .columnImage {
        width: 20vw;
        height: 20vw;
        padding: 20px;
        padding-bottom: 10px;
        background: #c4a991;
        border-radius: 100px;
        align-items: center;
    }

    .columnImage1920 {
        width: 35vw;
        height: 20vw;
        padding: 20px;
        padding-bottom: 1 0px;
        background: #c4a991;
        border-radius: 100px;
        align-items: center;
    }

    .columnImage1920full {
        width: 50vw;
        height: 28.5vw;
        padding: 20px;
        padding-bottom: 1 0px;
        background: #c4a991;
        border-radius: 40px;
        align-items: center;
    }

    .textPa {
        display: inline-block;
    }

    .youtubeBox {
        width: 50vw;
        height: 28.5vw;
    }



